body {
  background-color: #50464634;
  margin: 0;
}

:root {
  --primary-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.nav__item:last-child {
  margin: 0;
}

a {
  text-decoration: none;
}

.nav__link.is-active {
  color: var(--primary-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__link:hover {
  color: var(--primary-color);
}
